export const ProductType = {
  Bun: {
    type: 'bun',
    name: '',
  },
  Main: {
    type: 'main',
    name: '',
  },
  Sauce: {
    type: 'sauce',
    name: '',
  },
};

export function formatDate(string: string | number | Date) {
  return new Date(string).toLocaleString();
}

// eslint-disable-next-line consistent-return
export const checkStatus = (status: string) => {
  if (status === 'done') {
    return '';
  }
};

